.offers{
    width:77%;
    display: flex;
    margin: auto;
    padding: 0px 140px;
    margin-bottom: 150px;
    margin-top: 150px;
    background: linear-gradient(180deg,#fde1ff 0%,#e1ffea22 60%);
}
.offers-left{
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    line-height: 1.2;
}
.offers-left h1{
    color:#171717;
    font-size: 80px;
    font-weight: 600;
}
.offers-left p{
    color:#171717;
    font-size: 22px;
    font-weight: 600;
}
.offers-left button{
    width: 282px;
    height: 70px;
    border-radius: 35px;
    background:#ff4141;
    border: none;
    color: white;
    font-size: 22px;
    font-weight: 500;
    margin-top: 30px;
    cursor: pointer;
}
.offers-right{
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding-top: 50px;
}
@media (max-width:840px){
    .offers{
        width:77%;
        flex-direction: column;
        margin-top: 50px;
        margin-bottom: 40px;
        padding: 10px 45px;
    }
    .offers-left{
        line-height: 1.1;
    }
    .offers-left h1{
        font-size: 28px;
        font-weight: 600;
    }
    .offers-left p{
        font-size: 12px;
    }
    .offers-left button{
        width: 100px;
        height: 25px;
        border-radius: 30px;
        font-size: 10px;
        font-weight: 500;
        margin-top: 20px;
        cursor: pointer;
    }
    .offers-right{
        justify-content:center;
        padding-top: 30px;
    }
    .offers-right img{
        width: 200px;
    }
}
