.new-collections{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    margin-bottom: 100px;
}
.new-collections h1{
    color:#171717;
    font-size: 50px;
    font-weight: 600;
}
.new-collections hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background:#252525;
}
.collections{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-top: 50px;
    gap:30px;
}
@media (max-width:840px){

    .new-collections{
        margin-top: 30px;
        gap: 5px;
        margin-bottom: 70px;
    }
    .new-collections h1{
        font-size: 24px;
        font-weight: 600;
    }
    .new-collections hr{
        height: 3px;
    }
    .collections{
        display: grid;
        grid-template-columns: 1fr;
        margin-top: 35px;
        gap:25px;
    }

}